// button
.btn.btn-primary{
  background: $sec;
  padding: 11px 24px;
  border-radius: 8px;
  color: #2E3C52;
  border-color: $sec;
  font-size: 14px;
  font-weight: bold;
  transition: all 0.3s ease;
  &:hover, &:focus, &:active{
    box-shadow: none !important;
    color: #102E44 !important;
    background: darken($sec, 10%) !important;
  }
}

.btn.btn-green{
  background: #65B338;
  padding: 11px 24px;
  border-radius: 8px;
  color: #FFF;
  border-color: #65B338;
  font-size: 14px;
  font-weight: bold;
  transition: all 0.3s ease;
  &:hover, &:focus, &:active{
    box-shadow: none !important;
    color: #FFF !important;
    background: darken(#65B338, 10%) !important;
  }
}

.btn.btn-white{
  padding: 10px 40px;
  border-radius: 7px;
  color: rgba($color: #102E44, $alpha: 0.4);
  border-color: #F8F8F8;
  font-size: 16px;
  font-weight: bold;
  transition: all 0.3s ease;
  &:hover, &:focus, &:active{
    box-shadow: none !important;
    color: #102E44 !important;
    background: #F8F8F8 !important;
  }
}

.btn.btn-danger{
  background-color: transparent !important;
  background-image: none !important;
  color: #C50001;
  padding: 11px 24px;
  border-radius: 8px;
  border: 1px solid #C50001;
  font-size: 14px;
  font-weight: bold;
  transition: all 0.3s ease;
  &:hover, &:focus, &:active{
    box-shadow: none !important;
    color: #FFF !important;
    background-color: #C50001 !important;
  }
}

.btn.btn-black{
  background-color: transparent !important;
  background-image: none !important;
  color: #1A1A1A;
  padding: 11px 24px;
  border-radius: 8px;
  border: 1px solid #1A1A1A;
  font-size: 14px;
  font-weight: bold;
  transition: all 0.3s ease;
  &:hover, &:focus, &:active{
    box-shadow: none !important;
    color: #FFF !important;
    background-color: #1A1A1A !important;
  }
}

.right-btn{
  float: right;
  margin-bottom: -50px;
  margin-right: 20px;
  position: relative;
  top: 20px;
  z-index: 3;
}

.sidebar-dashboard{
  position: fixed;
  z-index: 9;
  left: 0;
  top: 0;
  bottom: 0;
  width: 250px;
  height: 100vh;
  background-color: $pri;
  padding: 15px;
  .logo-sidebar{
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    padding-left: 50px;
    margin-bottom: 25px;
    min-height: 42px;
    img{
      width: 40px;
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
    }
    h5{
      color: #FFF;
      margin-bottom: 0;
      font-size: 14px;
      font-weight: 800;
      font-family: $b;
    }
    p{
      font-size: 14px;
      color: #FFF;
      margin-bottom: 0;
    }
  }
  span.tmenu{
    display: block;
    color: #FFF;
    text-transform: uppercase;
    margin-bottom: 10px;
    font-size: 13px;
    letter-spacing: 1px;
  }
  .menu{
    padding: 0;
    li.nav-item{
      list-style: none;
      .nav-link{
        display: block;
        padding: 10px;
        border-radius: 7px;
        color: rgba($color: #FFF, $alpha: 0.6);
        position: relative;
        padding-left: 40px;
        font-size: 14px;
        font-weight: 500;
        transition: all 0.3s ease;
        letter-spacing: 1px;
        img{
          position: absolute;
          left: 10px;
          top: 50%;
          transform: translateY(-50%);
        }
        &:hover, &:focus, &.active{
          text-decoration: none;
          color: #2E3C52;
          background-color: $sec;
          img{
            filter: brightness(0) saturate(100%) invert(17%) sepia(8%) saturate(3445%) hue-rotate(178deg) brightness(94%) contrast(79%);;
          }
        }
      }
    }
  }
  .dropdown{
    position: absolute;
    left: 15px;
    right: 15px;
    bottom: 15px;
    .btn{
      padding-left: 55px;
      text-align: left;
      width: 100%;
      border: 1px solid rgba($color: #FFF, $alpha: 0.1);
      img{
        position: absolute;
        width: 50px;
        left: 10px;
        top: 50%;
        transform: translateY(-50%);
        width: 35px;
      }
      h5{
        font-size: 14px;
        margin-bottom: 0;
        font-weight: 700;
        color: #FFF;
      }
      span{
        display: block;
        font-size: 11px;
        color: rgba($color: #FFF, $alpha: 0.2);
      }
      &:after{
        display: none;
      }
    }
    .dropdown-menu{
      right: 0 !important;
    }
  }
}

main{
  padding-left: 290px;
  padding-right: 50px;
  padding-bottom: 70px;
  min-height: 100vh;
  position: relative;
  &::before{
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba($color: #BDC9DC, $alpha: 0.2);
    width: 100%;
    height: 244px;
    z-index: 1;
  }
  .wrap{
    position: relative;
    z-index: 2;
    .topbar{
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-bottom: 1px solid rgba($color: #565656, $alpha: 0.1);
      padding: 10px 0;
      margin-bottom: 15px;
      .breadcrumb{
        padding: 0;
        margin: 0;    
        background-color: transparent;
        .breadcrumb-item{
          color: rgba($color: #565656, $alpha: 0.6);
          display: block;
          font-size: 15px;
          a{
            color: #343434;
          }
          & + .breadcrumb-item::before{
            content: ">";
          } 
        } 
      }
      .koneksi{
        margin-left: auto;
        position: relative;
        padding-right: 35px;
        text-align: right;
        span{
          color: #828282;
          font-size: 11px;
          margin-bottom: 0;
          display: block;
        }
        h4{
          font-weight: 700;
          font-size: 13px;
          margin-bottom: 0;
        }
        img{
          position: absolute;
          right: 0;
          top: 50%;
          transform: translateY(-50%);
        }
      }
    }
    .title-page{
      margin: 30px 0;
      font-size: 30px;
      font-weight: 700;
      color: #1A1A1A;
      &.link{
        display: inline-block;
        img{
          display: inline-block;
          vertical-align: middle;
        }
        &:hover, &:focus{
          text-decoration: none;
        }
      }
    }
    .card-intro{
      padding: 40px 30px 40px 240px;
      border-radius: 18px;
      border: 1px solid #E6E6E6;
      position: relative;
      background-image: linear-gradient(to right, #E4EDFC 10%, #fff);
      margin-bottom: 30px;
      img{
        position: absolute;
        left: 25px;
        top: 50%;
        transform: translateY(-50%);
      }
      h3{
        font-weight: 700;
        font-size: 30px;
        color: #1A1A1A;
        margin-bottom: 10px;
      }
      p{
        font-size: 18px;
        color: rgba($color: #1A1A1A, $alpha: 0.6);
        margin-bottom: 0;
        line-height: 1.5;
      }
    }
    .card-overview{
      padding: 20px 20px;
      position: relative;
      border-radius: 18px;
      border: 1px solid #E6E6E6;
      span{
        display: block;
        margin-bottom: 5px;
        color: #828282;
      }
      h4{
        font-size: 26px;
        font-weight: 700;
        font-family: $b;
        margin-bottom: 0;
      }
      .circle{
        background-color: $sec;
        width: 46px;
        height: 46px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        right: 20px;
        top: 50%;
        transform: translateY(-50%);
      }
    }
    .card-wrap{
      background-color: #FFF;
      border-radius: 18px;
      padding: 20px;
      border: 1px solid #E6E6E6;
      .info-created{
        display: flex;
        justify-content: space-between;
        align-items: center;
        label{
          display: block;
          color: #565656;
          font-size: 14px;
          margin-bottom: 0;
        }
        h6{
          font-size: 14px;
          color: #565656;
          margin-bottom: 0;
        }
      }
      .info-section{
        margin-top: 30px;
        color: #565656;
        font-size: 15px;
        font-weight: bold;
      }
      .for-label{
        font-size: 14px;
        color: #565656;
        opacity: 0.5;
        display: block;
        margin-bottom: 0;
      }
      .value{
        font-size: 14px;
        color: #565656;
        display: block;
      }
      .change-password{
        margin-top: 30px;
        color: #1791CC;
        font-size: 15px;
        font-weight: bold;
        display: inline-block;
      }
      .form-group{
        label{
          margin-bottom: 5px;
          font-weight: bold;
          color: #565656;
        }
        .form-control{
          box-shadow: none;
          border: 1px solid #D9D9D9 !important;
          border-radius: 7px;
          height: auto;
          padding: 10px;
        }
        .add-new-url{
          font-weight: bold;
          color: #2E3C52;
          font-size: 15px;
          img{
            margin-right: 7px;
          }
        }
      }
    }
    .dashed{
      border-top: 1px dashed #E1E1E1;
      border-bottom: 1px dashed #E1E1E1;
    }
    .dataTables_wrapper{
      background-color: #FFF;
      border-radius: 18px;
      padding: 20px 0;
      border: 1px solid #E6E6E6;
      .dataTables_filter{
        float: left;
        margin-left: 20px;
        margin-bottom: 15px;
        label{
          color: #565656;
          font-size: 15px;
          input{
            border-radius: 7px;
            border-color: #D9D9D9;
            padding: 7px 5px 7px 25px;
            background-image: url('../images/search-normal.png');
            background-repeat: no-repeat;
            background-position: left 5px center;
            &:focus{
              outline: none !important;
            }
          }
        }
      }
      .dataTables_length{
        margin: 10px 0 0 20px;
        label{
          color: #565656;
          font-size: 15px;
          select{
            border-color: #D9D9D9;
            border-radius: 7px;
          }
        }
      }
      .dataTables_paginate{
        margin-right: 20px;
        .pagination{
          .page-item{
            .page-link{
              color: #102E44;
            }
            &.disabled{
              .page-link{
                color: #D4D4D4;
              }
            }
          }
        }
      }
      .table-overflow{
        overflow-x: auto;
        width: 100%;
      }
      .dataTable{
        border-bottom: 1px solid #E6E6E6;
        thead{
          tr{
            th{
              border-top: 1px solid #E6E6E6;
              border-bottom: none;
              padding: 15px 26px 15px 20px;
              font-size: 14px;
              color: #565656;
            }
            &.filters{
              th{
                background: #F1F4F7;
                input{
                  font-size: 14px;
                  color: #151515;
                  padding: 10px;
                  border-radius: 7px;
                  border: 1px solid #E2E2E2;
                  &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
                    color: #E2E2E2;;
                  }
                  &::-moz-placeholder { /* Firefox 19+ */
                    color: #E2E2E2;;
                  }
                  &:-ms-input-placeholder { /* IE 10+ */
                    color: #E2E2E2;;
                  }
                  &:-moz-placeholder { /* Firefox 18- */
                    color: #E2E2E2;;
                  }
                }
              }
            }
          }
        }
        tbody{
          tr{
            td{
              border-top: 1px solid #E6E6E6;
              font-size: 16px;
              padding: 20px;
              a{
                font-family: $b;
                font-weight: bold;
                color: #565656;
                transition: all 0.3s ease;
                img{
                  display: inline-block;
                  vertical-align: top;
                  margin-top: 4px;
                }
                &:hover, &:focus{
                  text-decoration: none;
                  color: #1A1A1A;
                }
              }
            }
          }
        }
      }
    }
  }
  

  .footer-dashboard{
    position: absolute;
    left: 290px;
    right: 0;
    bottom: 0px;
    padding: 15px 0;
    font-size: 13px;
    color: rgba($color: #565656, $alpha: 0.4);
    border-top: 1px solid rgba($color: #565656, $alpha: 0.1); 
  }
}

.custom-checkbox{
  position: relative;
  .custom-control-input{
    visibility: hidden;
    z-index: -1;
    position: absolute;
  }  
  .custom-control-label{
    font-weight: normal !important;
    position: relative;
    padding-left: 25px;
    &:before{
      content: "";
      width: 15px;
      height: 15px;
      border: 1px solid #e1e1e1;
      border-radius: 2px;
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
    }
    &::after{
      content: "";
      width: 15px;
      height: 15px;
      border-radius: 2px;
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      background-image: url('../images/check.png');
      background-repeat: no-repeat;
      background-position: center center;
      display: none;
    }
  }
  .custom-control-input:checked ~ .custom-control-label::after{
    display: block;
  }
  .custom-control-input:checked ~ .custom-control-label::before{
    background-color: $sec;
  }
  &:hover, &:focus{  
    .custom-control-label{
      cursor: pointer;
    }
  }
}

.modal{
  .modal-dialog{
    border-radius: 12px !important;
    .modal-header{
      border-bottom: none !important;
      .modal-title{
        font-family: $b;
        font-weight: bold;
      }
    }
    .modal-body{
      .list-header{
        padding-left: 2%;
        margin-bottom: 10px;
        label{
          font-size: 13px;
          font-weight: bold;
          font-size: $b;
          color: #1A1A1A;
          display: inline-block;
          margin-bottom: 0;
          vertical-align: middle;
          &:first-child{
            width: 24%;
          }
          &:nth-child(2){
            width: 24%;
          }
          &:nth-child(3){
            width: 24%;
          }
          &:last-child{
            width: 24%;
          }
        }
      }
      .list-item{
        border: 1px solid #e1e1e1;
        padding: 8px 10px;
        border-radius: 8px;
        margin-bottom: 5px;
        label{
          font-size: 13px;
          font-weight: normal;
          margin-bottom: 0;
          &:first-child{
            width: 24%;
          }
          &:nth-child(2){
            width: 24%;
          }
          &:nth-child(3){
            width: 24%;
          }
          &:last-child{
            width: 24%;
          }
        }
      }
    }
    .modal-footer{
      border-top: 1px dashed #e1e1e1;
    }
  }
}