// Header
header{
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 97;
  border-bottom: 1px solid rgba($color: #FDFDFD, $alpha: 0.2);
  transition: all 0.3s ease;
  .navbar{
    padding: 0;
    .navbar-brand{
      position: relative;
      padding-left: 80px;
      @include media-breakpoint-down(sm) {
        padding-left: 50px;
      }
      img{
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        @include media-breakpoint-down(sm) {
          width: 40px;
        }
      }
      h4{
        color: #FFF;
        font-weight: bold;
        font-family: $b;
        margin-bottom: 0;
        font-size: 16px;
      }
      span{
        color: #FFF;
        font-size: 14px;
        opacity: 0.6;
        display: block;
      }
    }
    .navbar-nav{
      flex-direction: row;
      .nav-item{
        .nav-link{
          font-size: 14px;
          color: #FFF;
          font-weight: 500;
          font-family: $r;
          display: block;
          padding: 25px 0;
          margin: 0 13px;
          letter-spacing: 1px;
          position: relative;
          &:after{
            content: "";
            position: absolute;
            left: 0;
            right: 0;
            bottom: 0;
            width: 100%;
            height: 4px;
            background-color: #8EC640;
            opacity: 0;
            transition: all 0.3s ease;
          }
          &:hover, &:focus, &.active{
            text-decoration: none;
            &:after{
              opacity: 1;
            }
          }
          &.hamburger{
            img{
              filter: brightness(0) saturate(100%) invert(99%) sepia(59%) saturate(0%) hue-rotate(112deg) brightness(112%) contrast(100%);;
            }
          }
        }
      }
    }
  }
  &.fixed{
    background-color: $pri;
  }
}


.side-menu {
  padding: 50px 60px;
  background-color: $pri;
  position: fixed;
  left: -720px;
  bottom: 0;
  top: 0;
  width: 720px;
  z-index: 99;
  transition: all 0.3s ease;
  @include media-breakpoint-down(md) {
    padding: 25px 30px;
  }
  @include media-breakpoint-down(sm) {
    width: 100%;
  }
  .close-btn {
    float: left;
  }
  .main-menu {
    float: left;
    width: 100%;
    margin-top: 70px;
    padding: 0;
    @include media-breakpoint-down(lg) {
      margin-top: 30px;
    }
    li {
      list-style: none;
      a {
        font-family: $b;
        font-size: 44px;
        color: #FFF;
        margin-bottom: 10px;
        @include media-breakpoint-down(md) {
          font-size: 36px;
        }
        &:hover,
        &:focus {
          text-decoration: none;
        }
      }
    }
  }
  &:after {
    content: "";
    display: table;
    clear: both;
  }
  &.slideIn {
    left: 0;
  }
}

.overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba($color: #000000, $alpha: 0.6);
  &.shown {
    display: block;
    z-index: 98;
  }
}

.hero{
  width: 100%;
  height: 100vh;
  text-align: center;
  background-size: cover;
  background-position: center center;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: -100px;
  @include media-breakpoint-down(md) {
    height: 450px;
    margin-bottom: -70px;
  }
  @include media-breakpoint-down(sm) {
    height: 400px;
    margin-bottom: -40px;
  }
  h1{
    color: #FFF;
    font-family: $b;
    font-weight: 800;
    font-size: 64px;
    width: 60%;
    margin: 0 auto 10px auto;
    @include media-breakpoint-down(md) {
      font-size: 42px;
      width: 80%;
    }
    @include media-breakpoint-down(sm) {
      font-size: 36px;
      width: 100%;
    }
  }
  p{
    color: #FFF;
    font-size: 18px;
    opacity: 0.6;
    font-weight: normal;
    width: 80%;
    margin: 0 auto 0px auto;
    @include media-breakpoint-down(md) {
      font-size: 16px;
      width: 100%;
    }
  }
}

.sertifikat{
  .img{
    border-radius: 24px;
    background-size: cover;
    background-position: center center;
    width: 100%;
    height: 380px;
    @include media-breakpoint-down(md) {
      height: 250px;
    }
  }
}

.artikel{
  padding: 100px 0 70px 0;
  @include media-breakpoint-down(md) {
    padding: 50px 0 35px 0;
  }
  .title-sect{
    color: $thi;
    text-align: center;
    margin-bottom: 20px;
    font-size: 40px;
    font-weight: 800;
    font-family: $b;
    @include media-breakpoint-down(md) {
      font-size: 32px;
    }
    @include media-breakpoint-down(sm) {
      font-size: 26px;
    }
  }
  .artikel-item{
    margin-bottom: 30px;
    @include media-breakpoint-down(sm) {
      margin-bottom: 15px;
    }
    .thumb{
      height: 335px;
      width: 100%;
      border-radius: 24px;
      background-size: cover;
      background-position: center center;
      margin-bottom: 20px;
      @include media-breakpoint-down(sm) {
        height: 230px;
        margin-bottom: 10px;
      }
    }
    .cat{
      color: #FEC22F;
      font-size: 14px;
      font-weight: 700;
      margin-bottom: 5px;
      display: block;
      @include media-breakpoint-down(sm) {
        font-size: 12px;
      }
    }
    .title{
      display: block;
      color: #000000;
      font-size: 32px;
      margin-bottom: 5px;
      font-weight: 700;
      line-height: 1.3;
      transition: all 0.3s ease;
      @include media-breakpoint-down(md) {
        font-size: 26px;
      }
      @include media-breakpoint-down(sm) {
        font-size: 22px;
      }
      &:hover, &:focus{
        text-decoration: none;
        color: $sec;
      }
    }
    p{
      color: #000000;
      font-size: 18px;
      opacity: 0.6;
      margin-bottom: 5px;
      @include media-breakpoint-down(sm) {
        font-size: 14px;
      }
    }
    .date{
      font-size: 14px;
      color: #000000;
      opacity: 0.6;
      display: inline-block;
      vertical-align: middle;
    }
    .by{
      font-size: 14px;
      color: #000000;
      opacity: 0.6;
      display: inline-block;
      vertical-align: middle;
      &::before{
        content: "●";
        color: #000;
        margin-left: 3px;
        margin-right: 5px;
        font-size: 10px;
      }
    }
    &.small{  
      .cat{
        font-size: 12px;
      }
      .thumb{
        height: 220px;
        @include media-breakpoint-down(sm) {
          height: 240px;
        }
      }
      .title{
        font-size: 24px;
        @include media-breakpoint-down(md) {
          font-size: 20px;
        }
        @include media-breakpoint-down(sm) {
          font-size: 22px;
        }
      }
      p{
        font-size: 16px;
        @include media-breakpoint-down(sm) {
          font-size: 14px;
        }
      }
    }
  }
}

footer{
  .container{
    border-top: 1px solid #EBEBEB;
    padding: 30px 15px;
    .logo-footer{
      position: relative;
      padding-left: 80px;
      img{
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
      }
      h4{
        font-weight: bold;
        font-family: $b;
        margin-bottom: 0;
        font-size: 16px;
        color: #222832;
      }
      span{
        color: #222832;
        font-size: 14px;
        opacity: 0.6;
        display: block;
      }
    }
    .socmed{
      text-align: right;
      margin-top: 25px;
      @include media-breakpoint-down(sm) {
        text-align: left;
      }
      a{
        display: inline-block;
        vertical-align: middle;
        color: #222832;
        margin: 0 5px;
        i{
          width: 16px;
        }
      }
    }
    p.copyright{
      margin-top: 30px;
      font-size: 12px;
      color: #222832;
      opacity: 0.4;
      margin-bottom: 0;
      @include media-breakpoint-down(sm) {
        margin-top: 15px;
      }
    }
    .other{
      text-align: right;
      @include media-breakpoint-down(sm) {
        text-align: left;
      }
      .other-link{
        color: #222832;
        opacity: 0.4;
        font-size: 12px;
        display: inline-block;
        margin-top: 30px;
        @include media-breakpoint-down(sm) {
          margin-top: 15px;
        }
      }
    }
  }
}

.shortcut{
  .card{
    padding: 25px;
    border-radius: 24px;
    background-color: #FFF;
    margin-bottom: 30px;
    border: 1px solid #F3F3F3;
    background-position: right 20px bottom 20px;
    background-repeat: no-repeat;
    min-height: 390px;
    @include media-breakpoint-down(lg) {
      background-size: 200px;
    }
    @include media-breakpoint-down(sm) {
      padding: 15px;
      background-size: 100px;
      background-position: center bottom 5px;
      min-height: 370px;
      margin-bottom: 15px;
    }
    h3{
      color: $thi;
      font-size: 44px;
      margin-bottom: 5px;
      font-weight: bold;
      font-family: $b;
      @include media-breakpoint-down(sm) {
        font-size: 36px;
      }
    }
    p{
      color: #A5A6AA;
      font-size: 16px;
      margin-bottom: 15px;
      @include media-breakpoint-down(sm) {
        font-size: 14px;
      }
    }
    .shortcut-link{
      float: left;
      padding: 10px 25px;
      border-radius: 30px;
      border: 1.5px solid #1791CC;
      color: #1791CC;
      font-size: 16px;
      clear: left;
      margin-bottom: 10px;
      font-weight: 500;
      transition: all 0.3s ease;
      @include media-breakpoint-down(sm) {
        font-size: 14px;
        clear: none;
        margin-bottom: 5px;
        margin-right: 5px;
      }
      img{
        margin-left: 5px;
        display: inline-block;
        vertical-align: middle;
      }
      &:hover, &:focus{
        text-decoration: none;
        border-color: $pri;
      }
    }
    &:after{
      content: "";
      display: table;
      clear: both;
    }
  }
}